import React, { useState, useEffect } from "react";

const Days = [
  { key: 0, name: "Sunday", code: "Su" },
  { key: 1, name: "Monday", code: "Mo" },
  { key: 2, name: "Tuesday", code: "Tu" },
  { key: 3, name: "Wednesday", code: "We" },
  { key: 4, name: "Thursday", code: "Th" },
  { key: 5, name: "Friday", code: "Fr" },
  { key: 6, name: "Saturday", code: "Sa" }
];

const Months = [
  { key: 0, name: "January" },
  { key: 1, name: "February" },
  { key: 2, name: "March" },
  { key: 3, name: "April" },
  { key: 4, name: "May" },
  { key: 5, name: "June" },
  { key: 6, name: "July" },
  { key: 7, name: "August" },
  { key: 8, name: "September" },
  { key: 9, name: "October" },
  { key: 10, name: "November" },
  { key: 11, name: "December" }
];

const Calendar = props => {
  const [date, setdate] = useState(new Date());
  const [CurrentMonth, setCurrentMonth] = useState({
    startDate: {},
    endDate: {},
    startDay: "",
    endDay: "",
    name: ""
  });
  const [NextMonth, setNextMonth] = useState({
    startDate: 0,
    endDate: 0,
    startDay: "",
    endDay: "",
    name: ""
  });
  const [D, setD] = useState(date.getDay());
  const [M, setM] = useState(date.getMonth());
  const [Y, setY] = useState(date.getFullYear());
  const [CurrentDates, setCurrentDates] = useState([]);
  const [NextDates, setNextDates] = useState([]);
  const [Booking, setBooking] = useState({
    start: "",
    end: ""
  });

  const [SelectedDate, setSelectedDate] = useState({
    date: {
      start: null,
      end: null
    },
    month: {
      start: null,
      end: null
    }
  });
  const [HoverDate, setHoverDate] = useState({
    date: null,
    month: null
  });

  const display = () => {
    return props.visible === true ? "block" : "none";
  };

  useEffect(() => {
    getDates();
  }, []);

  const getDates = () => {
    let firstOfCurrentMonth = new Date(Y, M, 1);
    let lastOfCurrentMonth = new Date(Y, M + 1, 0);
    let firstOfNextMonth = new Date(Y, M + 1, 1);
    let lastOfNextMonth = new Date(Y, M + 2, 0);

    let c = { ...CurrentMonth };
    let n = { ...NextMonth };

    c.startDate = firstOfCurrentMonth.getDate();
    c.endDate = lastOfCurrentMonth.getDate();
    c.name = Months[M].name;
    c.startDay = firstOfCurrentMonth.getDay();
    c.endDay = lastOfCurrentMonth.getDay();

    n.startDate = firstOfNextMonth.getDate();
    n.endDate = lastOfNextMonth.getDate();
    n.name = Months[M + 1].name;
    n.startDay = firstOfNextMonth.getDay();
    n.endDay = lastOfNextMonth.getDay();

    setCurrentMonth(c);
    setNextMonth(n);
    resetDates(c, n);
  };

  const RenderDays = Days.map((day, index) => {
    return (
      <div
        className="calendar__day--name"
        key={index}
        data-day__name={day.name}
      >
        {day.code}
      </div>
    );
  });

  const resetDates = (currentMonth, nextMonth) => {
    let currentDates = [];
    let nextDates = [];

    if (currentMonth.startDay > 0) {
      for (let j = 0; j < currentMonth.startDay; j++) {
        currentDates.push(0);
      }
    }
    for (let i = 0; i < currentMonth.endDate; i++) {
      currentDates.push(i + 1);
    }
    if (nextMonth.startDay > 0) {
      for (let j = 0; j < nextMonth.startDay; j++) {
        nextDates.push(0);
      }
    }
    for (let i = 0; i < nextMonth.endDate; i++) {
      nextDates.push(i + 1);
    }

    setCurrentDates(currentDates);
    setNextDates(nextDates);
  };

  const setSelected = e => {
    let val = parseInt(e.target.innerHTML);
    let mval = parseInt(e.target.getAttribute("data-month"));

    let s = { ...SelectedDate };

    if (s.month.end !== null) {
      s.month.end = null;
      s.month.start = mval;
    } else {
      if (s.month.start === null) {
        s.month.start = mval;
      } else {
        s.month.end = mval;
      }
    }

    if (s.date.start === null || s.month.start === s.month.end) {
      if (s.date.start === null || s.date.end === null) {
        if (s.date.end !== null) {
          s.date.end = null;
          s.date.start = val;
        } else {
          if (s.date.start === null) {
            s.date.start = val;
          } else {
            if (val < s.date.start) {
              s.date.start = val;
              s.date.end = null;
            } else {
              s.date.end = val;
            }
          }
        }
      }
    } else if (s.month.start !== s.month.end && s.month.end !== null) {
      s.date.end = val;
    } else {
      s.month.end = null;
      s.date.end = null;
      s.date.start = val;
    }

    let b = { ...Booking };
    let date = "";
    if (
      (s.month.start === M && s.month.end === null) ||
      (s.month.start === M && s.month.end === M)
    ) {
      date = new Date(Y, M, val);
    }
    if (
      (s.month.start === M && s.month.end === M + 1) ||
      (s.month.start === M + 1 && s.month.end === null)
    ) {
      date = new Date(Y, M + 1, val);
    }

    if (b.start === "" || s.month.start === s.month.end) {
      if (b.end !== "") {
        b.start = date;
        b.end = "";
        props.setCheckIn(date);
        props.setCheckOut("Check-out");
      } else {
        if (b.start === "") {
          b.start = date;
          props.setCheckIn(date);
        } else {
          if (date < b.start) {
            b.start = date;
            props.setCheckIn(date);
          } else {
            b.end = date;
            props.setCheckOut(date);
          }
        }
      }
    } else if (
      b.start !== "" &&
      s.month.start !== s.month.end &&
      s.month.end !== null
    ) {
      console.log(s);
      b.end = date;
      props.setCheckOut(date);
    } else {
      b.end = "";
      b.start = date;
      props.setCheckOut("Check-out");
      props.setCheckIn(date);
    }

    setSelectedDate(s);
    setBooking(b);
  };

  const handleHover = (c, m) => {
    let h = { ...HoverDate };
    if (Booking.start !== "") {
      h.date = c;
      h.month = m;
    }
    setHoverDate(h);
  };

  const handleLeave = () => {
    let h = { date: null, month: null };
    setHoverDate(h);
  };

  const RenderCurrentDates = CurrentDates.map((c, index) => {
    return (
      <div
        data-month={M}
        onClick={setSelected}
        onMouseEnter={handleHover.bind(this, c, M)}
        onMouseLeave={handleLeave}
        key={index}
        className={`
        ${
          SelectedDate.date.end !== null && SelectedDate.month.end !== M
            ? ""
            : c === HoverDate.date && M === HoverDate.month
            ? "hovered"
            : ""
        }
          
            ${
              c === SelectedDate.date.start && M === SelectedDate.month.start
                ? "selected selected--start"
                : c === SelectedDate.date.end && M === SelectedDate.month.end
                ? "selected selected--end"
                : ""
            }
        ${
          SelectedDate.month.start !== SelectedDate.month.end &&
          SelectedDate.month.end !== null &&
          c > SelectedDate.date.start
            ? "in-range"
            : ""
        }
            ${
              SelectedDate.month.start === SelectedDate.month.end &&
              SelectedDate.date.end !== null &&
              SelectedDate.month.end !== M
                ? ""
                : ((M === SelectedDate.month.start ||
                    M === SelectedDate.month.end) &&
                    c > SelectedDate.date.start &&
                    c < SelectedDate.date.end) ||
                  (c > SelectedDate.date.start &&
                    c < HoverDate.date &&
                    M === HoverDate.month)
                ? "in-range"
                : ""
            }
        calendar__date calendar__date--${
          c === 0
            ? "empty"
            : c === D + 1
            ? "today"
            : c < D + 1
            ? "past"
            : "future"
        }
        ${c < HoverDate && c > SelectedDate.date.start ? "in-range" : ""}`}
      >
        {c === 0 ? "" : c}
      </div>
    );
  });

  const RenderNextDates = NextDates.map((c, index) => {
    return (
      <div
        data-month={M + 1}
        onClick={setSelected}
        onMouseEnter={handleHover.bind(this, c, M + 1)}
        onMouseLeave={handleLeave}
        key={index}
        className={`
        ${
          SelectedDate.date.end !== null && SelectedDate.month.end !== M + 1
            ? ""
            : c === HoverDate.date && M + 1 === HoverDate.month
            ? "hovered"
            : ""
        }
         ${
           c === SelectedDate.date.start && M + 1 === SelectedDate.month.start
             ? "selected selected--start"
             : c === SelectedDate.date.end && M + 1 === SelectedDate.month.end
             ? "selected selected--end"
             : ""
         }
        
        ${
          SelectedDate.month.start !== SelectedDate.month.end
            ? c < SelectedDate.date.end
              ? "in-range"
              : ""
            : SelectedDate.date.end !== null && SelectedDate.month.end !== M + 1
            ? ""
            : ((M + 1 === SelectedDate.month.start ||
                M + 1 === SelectedDate.month.end) &&
                c > SelectedDate.date.start &&
                c < SelectedDate.date.end) ||
              (c > SelectedDate.date.start &&
                c < HoverDate.date &&
                M + 1 === HoverDate.month)
            ? "in-range"
            : ""
        }


        calendar__date calendar__date--${
          c === 0 ? "empty" : c === D + 1 ? "" : c < D + 1 ? "" : "future"
        }
        ${c < HoverDate && c > SelectedDate.date.start ? "in-range" : ""}`}
      >
        {c === 0 ? "" : c}
      </div>
    );
  });

  return (
    <div style={{ display: `${display()}` }} className="calendar">
      <div className="calendar__control calendar__control--prev"></div>
      <div className="calendar__control calendar__control--next"></div>
      <div className="calendar__content">
        <div className="calendar__wrapper">
          <div className="calendar__month">{CurrentMonth.name}</div>
          <div className="calendar__dates">
            <div className="calendar__head calendar__row">
              <div className="calendar__days">{RenderDays}</div>
            </div>
            <div className="calendar__body">{RenderCurrentDates}</div>
          </div>
        </div>
        <div className="calendar__wrapper">
          <div className="calendar__month">{NextMonth.name}</div>
          <div className="calendar__dates">
            <div className="calendar__head calendar__row">
              <div className="calendar__days">{RenderDays}</div>
            </div>
            <div className="calendar__body">{RenderNextDates}</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Calendar;
